// src/extensions/FiltrosVisuales.js
class FiltrosVisuales extends Autodesk.Viewing.Extension {
    load() {
        // Aqu se aade la lgica para cargar la extensin, como agregar botones al toolbar.
        return true;
    }

    unload() {
        // Aqu se aade la lgica para descargar la extensin, como eliminar botones.
        return true;
    }
}

Autodesk.Viewing.theExtensionManager.registerExtension('FiltrosVisuales', FiltrosVisuales);
